/* Import */

@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400,600,700|Oswald:400,300,700);

/* Variables */

$background: #252827;

$color-primary: #f56255;
$color-light: white;
$spacing: 30.75px;
$radius: 4px;

$date: 120px;
$dotborder: 4px;
$dot: 15px;
$line: 2px;

@media(min-width: 320px) {
    .schedule-container {
        // min-width: 100%;
        background: linear-gradient(120deg, #fc734f, #e43762);
        // padding-top: 6.25rem;
        padding-top: 3.104375rem;
        padding-bottom: 2.975rem;

        .main-title {
            font-size: 2.75rem;
            line-height: 3.25rem;
            margin-bottom: 1.791875rem;
        }

        .schedule-under-h {
            font-size: 1.25rem;
            margin-bottom: 3.85625rem;
        }
    }

    .timeline-container {
        background: #fff;
        border-radius: 0.875rem;
        width: 100%;
        -webkit-border-radius: 0.875rem;
        -moz-border-radius: 0.875rem;
        -ms-border-radius: 0.875rem;
        -o-border-radius: 0.875rem;
        box-shadow: 0.3125rem 0.3125rem 3.75rem rgba(0, 0, 0, 0.1);
        padding-top: 4.4375rem;
        padding-bottom: 2.925rem;


        .timeline {
            display: block;
            border-left: $line solid $color-primary;
            position: relative;
            padding: 0 0 0 1.25rem;
            list-style: none;
            text-align: left;
            left: 25px;
            margin-bottom: 25px;
            z-index: 100;
            margin-right: -20px;
            max-width: 80%;

            ol {
                font-size: 1.125rem;
                padding-left: 20px;
                line-height: 1.35rem;
            }

            li {
                margin-bottom: 1.125rem;
            }
        }
    }

    // 

    h5 {
        color: #7a7a7a;
        font-size: 18px;
    }

    .event {
        position: relative;
        top: 0;
        // margin-bottom: 67.7px;
        margin-bottom: 40px;

        &:last-of-type {
            padding-bottom: 0;
            margin-bottom: 0;
            border: none;
        }

        // &:before,
        &:after {
            position: absolute;
            display: block;
            top: 0;
        }

        &:before {
            left: ((($date * 0.6) + $spacing + $line + $dot + ($dotborder * 2)) * 1.5) * -1;
            color: #000;
            font-family: 'Gotham Pro', sans-serif;
            font-size: 18px;
            letter-spacing: 0.002em;
            content: attr(data-date);
            text-align: right;
            font-weight: 100;
            min-width: $date;
            top: 5px;
        }

        &:after {
            left: -28.5px;
            background: $color-primary;
            border-radius: 50%;
            height: $dot;
            width: $dot;
            content: "";
            top: 5px;
        }

        .event-hidden-text {
            margin-bottom: 1.5625rem;
        }

    }

    .reg-event {
        margin-bottom: 48.7px;

        .event-section {
            position: relative;
            // top: -19px;
        }

        &:after {
            left: -36.5px;
            background: radial-gradient(circle at center, #f56255 0%, #f56255 40%, rgba(245, 98, 85, 0.35) 40%, rgba(245, 98, 85, 0.35) 100%);
            border-radius: 50%;
            height: $dot*2;
            width: $dot*2;
            content: "";
            z-index: 120;
            top: -7px;
        }

        // &:before {
        //     top: -25px;
        // }

    }

    .last-event {
        margin-bottom: 0;

        .event-section {
            position: relative;
            top: 13px;
            margin-bottom: 0;
        }

        &:after {
            left: -36.5px;
            background: radial-gradient(circle at center, #f56255 0%, #f56255 40%, rgba(245, 98, 85, 0.35) 40%, rgba(245, 98, 85, 0.35) 100%);
            border-radius: 50%;
            height: $dot*2;
            width: $dot*2;
            content: "";
            z-index: 120;
            top: 15px;
        }
    }

    .event-section {
        font-family: 'Gotham Pro', sans-serif;
        font-weight: 400;
        font-size: 1.5rem;
        letter-spacing: 0.002em;
    }

    .event-title {
        font-size: 1.25rem;
    }

    .event-show-more {
        color: #f56255;
        cursor: pointer;
    }
}


@media(min-width: 992px) {
    .schedule-container {
        padding-top: 6.25rem;
        padding-bottom: 7.1875rem;

        .main-title {
            font-size: 3rem;
            line-height: 4.375rem;
            margin-bottom: 1rem;
        }

        .schedule-under-h {
            font-size: 1.25rem;
            margin-bottom: 3.75rem;
        }
    }

    .timeline-container {
        background: #fff;
        border-radius: 0.875rem;
        width: 100%;
        -webkit-border-radius: 0.875rem;
        -moz-border-radius: 0.875rem;
        -ms-border-radius: 0.875rem;
        -o-border-radius: 0.875rem;
        box-shadow: 0.3125rem 0.3125rem 3.75rem rgba(0, 0, 0, 0.1);

        .col-12 {
            padding: 5.0625rem 4.875rem 5.875rem 17.625rem;
        }



        .timeline {
            left: 12rem;
            max-width: 70%;
            margin-right: 0;

            .event {
                position: relative;
                top: 0;
                margin-bottom: 67.7px;

                &:last-of-type {
                    padding-bottom: 0;
                    margin-bottom: 0;
                    border: none;
                }

                &:before,
                &:after {
                    position: absolute;
                    display: block;
                    top: 0;
                }

                &:before {
                    left: ((($date * 0.6) + $spacing + $line + $dot + ($dotborder * 2)) * 1.5) * -1;
                    color: #000;
                    font-family: 'Gotham Pro', sans-serif;
                    font-size: 18px;
                    letter-spacing: 0.002em;
                    content: attr(data-date);
                    text-align: right;
                    font-weight: 100;
                    min-width: $date;
                    top: 5px;
                }

                &:after {
                    background: $color-primary;
                    border-radius: 50%;
                    height: $dot;
                    width: $dot;
                    content: "";
                    top: 10px;
                }

                .event-hidden-text {
                    margin-bottom: 1.5625rem;
                }

            }

            .reg-event {
                margin-bottom: 48.7px;

                .event-section {
                    position: relative;
                    top: -19px;
                }

                &:after {
                    background: radial-gradient(circle at center, #f56255 0%, #f56255 40%, rgba(245, 98, 85, 0.35) 40%, rgba(245, 98, 85, 0.35) 100%);
                    border-radius: 50%;
                    height: $dot*2;
                    width: $dot*2;
                    content: "";
                    z-index: 120;
                    top: -15px;
                }

                &:before {
                    top: -12px;
                }

            }

            .reg-event {
                margin-bottom: 48.7px;
        
                .event-section {
                    position: relative;
                    top: -19px;
                }
        
                &:after {
                    background: radial-gradient(circle at center, #f56255 0%, #f56255 40%, rgba(245, 98, 85, 0.35) 40%, rgba(245, 98, 85, 0.35) 100%);
                    border-radius: 50%;
                    height: $dot*2;
                    width: $dot*2;
                    content: "";
                    z-index: 120;
                    top: -15px;
                }
        
                &:before {
                    top: -12px;
                }
        
            }
        
            .last-event {
        
                .event-section {
                    position: relative;
                    top: 13px;
                }
        
                &:after {
                    background: radial-gradient(circle at center, #f56255 0%, #f56255 40%, rgba(245, 98, 85, 0.35) 40%, rgba(245, 98, 85, 0.35) 100%);
                    border-radius: 50%;
                    height: $dot*2;
                    width: $dot*2;
                    content: "";
                    z-index: 120;
                    top: 15px;
                }
            }
        
            .event-section {
                font-family: 'Gotham Pro', sans-serif;
                font-weight: 400;
                font-size: 1.5rem;
                letter-spacing: 0.002em;
            }
        
            .event-title {
                font-size: 1.25rem;
            }
        
            .event-show-more {
                color: #f56255;
                cursor: pointer;
            }
        }
    }
}